// ListPage.js
import { useState, useEffect } from 'react';
import { Modal, Button, Table } from 'react-bootstrap';
import './ListPage.css';
 const logoImg = "/logo192.png";
const imageGitHub = "/guthub-milou-med.PNG";

function ListPage({ patients = [], onEdit, onDelete }) {
  const [showDeleteConfirm, setShowDeleteConfirm] = useState(false);
  const [deleteIndex, setDeleteIndex] = useState(null);

  const [showColumns, setShowColumns] = useState(false);
  const [visibleColumns, setVisibleColumns] = useState(() => {
    const saved = localStorage.getItem("visibleColumns");
    return saved ? JSON.parse(saved) : {
      dateEntree: true, dateSortie: true, vacc1: true, vacc2: true,
      sterilisation: true, vermifuges: true, poids: true, visiteVet: true,
      paiement: true, ratation: true,
    };
  });

  useEffect(() => {
    localStorage.setItem("visibleColumns", JSON.stringify(visibleColumns));
  }, [visibleColumns]);

  // Fonction pour gnrer le PDF
  // Fonction pour gnrer le PDF
  // Fonction pour gnrer le PDF
  const generatePDF = (patient) => {
    // Crer une nouvelle fentre pour l'impression
    const printWindow = window.open('', '_blank');

    // Obtenir la date et l'heure actuelles
    const now = new Date();
    const dateStr = now.toLocaleDateString('fr-MA');
    const timeStr = now.toLocaleTimeString('fr-MA');

    // Construire le contenu HTML du PDF
    const content = `
    <!DOCTYPE html>
    <html>
    <head>
      <link rel="icon" href="${logoImg}" />
      <title>Fiche Chat - ${patient.nom}</title>
      <style>
        * {
          margin: 0;
          padding: 0;
          box-sizing: border-box;
        }
        
        /* DSACTIVER LES EN-TTES ET PIEDS DE PAGE DU NAVIGATEUR */
        @page {
          margin: 0;
          size: A4;
        }
        body { 
           font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif; 
          margin: 0;
          padding: 20px;
          color: #2c3e50;
          background: white;
          line-height: 1.5;
          -webkit-print-color-adjust: exact !important;
          print-color-adjust: exact !important;
          margin-bottom: 86px;
        }
        
        .container {
          max-width: 1000px;
          margin: 0 auto;
          background: white;
          padding: 5px;
        }
        
        .header { 
          text-align: center; 
          border-bottom: 3px solid #4CAF50; 
          padding-bottom: 15px;
          margin-bottom: 25px;
        }
        
        .header h1 { 
          color: #2c3e50; 
          margin: 0;
          font-size: 2em;
          font-weight: 700;
        }
        
        .header .subtitle {
          color: #7f8c8d;
          font-size: 1em;
          margin-top: 5px;
        }
        
        .patient-info { 
          margin-bottom: 25px; 
          background: #f8f9fa; 
          padding: 20px;
          border-radius: 8px;
          border: 1px solid #e0e0e0;
        }
        
        .patient-info h2 {
          color: #2c3e50;
          margin-bottom: 15px;
          font-size: 1.3em;
          text-align: center;
          font-weight: 600;
        }
        
        .info-grid {
          display: grid;
          grid-template-columns: 1fr 1fr 1fr;
          gap: 15px;
          margin-top: 15px;
        }
        
        .info-item {
          background: white;
          padding:2px  10px;
          border-radius: 6px;
          border: 1px solid #ddd;
        }
        
        .info-label {
          font-weight: 700;
          color: #000000ff;
          display: block;
          margin-bottom: 5px;
          font-size: 0.85em;
          border-bottom: 1px dashed;
        }
        
        .info-value {
          color: #34495e;
          font-weight: 500;
          font-size: 0.95em;
        }
        
        .sections-grid {
          display: grid;
          grid-template-columns: 1fr 1fr;
          gap: 20px;
          margin-bottom: 25px;
        }
        
        .section { 
          background: white;
          border: 1px solid #e0e0e0;
          border-radius: 8px;
          page-break-inside: avoid;
          min-height: 200px;
          display: flex;
          flex-direction: column;
        }
        
        .section-title { 
          color: white; 
          padding: 10px; 
          border-radius: 8px 8px 0 0;
          font-weight: 700;
          font-size: 1em;
          min-height: 50px;
          display: flex;
          align-items: center;
          border-bottom: 2px solid #000000ff;
        }
        
        .section-title.poids {
          background: #dc3545;
        }
        
        .section-title.vermifuges {
          background: #ffc107;
          color: #000;
        }
        
        .section-title.visites {
          background: #17a2b8;
        }
        
        .section-title.paiements {
          background: #28a745;
        }
        
        .section-content {
          padding: 15px;
          flex-grow: 1;
          max-height: 250px;
          overflow-y: auto;
          background: #f8f9fa;
        }
        
        .list-item {
          background: white;
          padding: 2px 8px;
          margin-bottom: 5px;
          border-radius: 5px;
          font-size: 0.9em;
          border-left: 4px solid #4CAF50;
          border: 1px solid #e0e0e0;
        }
        
        .list-item strong {
          color: #2c3e50;
          font-weight: 600; 
          margin-bottom: 3px;
          font-size: 0.9em;
        }
        
        .list-item span {
          color: #000000ff;
          line-height: 1.4;
        }
        
        .empty-section {
          text-align: center;
          color: #95a5a6;
          font-style: italic;
          padding: 30px 20px;
          flex-grow: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          font-size: 0.9em;
          background: white;
        }
        
        .footer { 
          border-top: 2px solid #ecf0f1;
     
          color: #7f8c8d;
          font-size: 0.8em;
          display: flex;
          justify-content: space-between;
          align-items: center;
         
        }
        
        .footer-left {
          text-align: left;
        }
        
        .footer-right {
          text-align: right;
        }
        
        .footer p {
          margin: 2px 0;
        }

             .footer-center   p  
              {
              text-aline :center !important;
              display: flex;
              justify-content: center;
              align-items: center;
              gap:15px
              }  
               
        
        .columns-container {
          display: flex;
          gap: 10px;
        }
        
        .column {
          flex: 1;
          display: flex;
          flex-direction: column;
          gap: 10px;
        }
        
        /* Scrollbar simple */
        .section-content::-webkit-scrollbar {
          width: 4px;
        }
        
        .section-content::-webkit-scrollbar-track {
          background: #f1f1f1;
        }
        
        .section-content::-webkit-scrollbar-thumb {
          background: #4CAF50;
        }
        
     @media print {
          /* SUPPRIMER LES EN-TTES ET PIEDS DE PAGE PAR DFAUT */
          @page {
            margin: 0;
            size: A4;
          }
          
          body {
            margin: 0;
            padding: 15mm;
            -webkit-print-color-adjust: exact !important;
            print-color-adjust: exact !important;
          }
          
          .no-print { 
            display: none; 
          }
          
          .container {
            padding: 5px;
            margin: 0; 
          }
          
          .section-content {
            max-height: none;
            overflow: visible;
          }
          
          .section {
            min-height: auto;
         page-break-inside: avoid;   
          }

              .footer {
              
          display: flex;
          justify-content: space-between;
          align-items: center;
              position: fixed;
              bottom: 4px;
              left: 4px;
            width: 90%;
              max-width: 90%;
              transform: translateX(5%);
              margin:auto;
              }
        }
              .logo-print{
                position: fixed;
              top: 4px;
              left: 4px; 
              border-radius: 50%;
              }
            
             
      </style>
    </head>
    <body>
    
      <div class="container">
   ${logoImg ? `<img src="${logoImg}" width="100px" height="100px" class="logo-print" />` : ''}
        <div class="header">
          <h1> Chat de Hamza</h1>
          <div class="subtitle">Fiche mdicale complte</div>
        </div>

        <div class="patient-info">
          <h2>Informations du chat</h2>
          <div class="info-grid">
            <div class="info-item">
              <span class="info-label">Nom</span>
              <span class="info-value">${patient.nom || 'Non spcifi'}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Date d'entre</span>
              <span class="info-value">${patient.dateEntree || 'Non spcifie'}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Date de sortie</span>
              <span class="info-value">${patient.dateSortie || 'Non spcifie'}</span>
            </div>
            <div class="info-item">
              <span class="info-label">1re vaccination</span>
              <span class="info-value">${patient.vaccination1 || 'Non spcifie'}</span>
            </div>
            <div class="info-item">
              <span class="info-label">2me vaccination</span>
              <span class="info-value">${patient.vaccination2 || 'Non spcifie'}</span>
            </div>
            <div class="info-item">
              <span class="info-label">Strilisation</span>
              <span class="info-value">${patient.sterilisation || 'Non spcifie'}</span>
            </div>
          </div>
        </div> 
          
       

        <div class="sections-grid">
          <!-- Colonne de gauche -->
          <div class="column">
            <!-- Poids - Rouge danger -->
            <div class="section">
              <div class="section-title poids"> Historique du poids</div>
              <div class="section-content">
                ${patient.poids && patient.poids.length > 0 ?
        patient.poids.map(poids => `
                  <div class="list-item">
                    <strong>${poids.date || 'Date non spcifie'}</strong> : 
                    <span>${poids.valeur || '0'} kg</span>
                  </div>
                `).join('')
        :
        '<div class="empty-section">Aucun enregistrement de poids</div>'
      }
              </div>
            </div>

            <!-- Visites vtrinaires - Bleu info -->
            <div class="section">
              <div class="section-title visites"> Visites vtrinaires</div>
              <div class="section-content">
                ${patient.visitesVeterinaires && patient.visitesVeterinaires.length > 0 ?
        patient.visitesVeterinaires.map(visite => `
                  <div class="list-item">
                    <strong>${visite.date || 'Date non spcifie'}</strong> :
                    <span>${visite.remarque || 'Aucune remarque'}</span>
                  </div>
                `).join('')
        :
        '<div class="empty-section">Aucune visite vtrinaire</div>'
      }
              </div>
            </div>
          </div>

          <!-- Colonne de droite -->
          <div class="column">
            <!-- Vermifuges - Jaune warning -->
            <div class="section">
              <div class="section-title vermifuges"> Vermifuges</div>
              <div class="section-content">
                ${patient.vermifuges && patient.vermifuges.length > 0 ?
        patient.vermifuges.map(vermifuge => `
                  <div class="list-item">
                    <strong>${vermifuge.date || 'Date non spcifie'}</strong> :
                    <span>${vermifuge.remarque || 'Aucune remarque'}</span>
                  </div>
                `).join('')
        :
        '<div class="empty-section">Aucun vermifuge administr</div>'
      }
              </div>
            </div>

            <!-- Paiements - Vert success -->
            <div class="section">
              <div class="section-title paiements"> Paiements</div>
              <div class="section-content">
                ${patient.paiements && patient.paiements.length > 0 ?
        patient.paiements.map(paiement => `
                  <div class="list-item">
                    <strong>${paiement.date || 'Date non spcifie'}</strong> :
                    <span>${paiement.details || 'Aucun dtail'}</span>
                  </div>
                `).join('')
        :
        '<div class="empty-section">Aucun paiement enregistr</div>'
      }
              </div>
            </div>
          </div>
        </div>

        <div class="footer">
          <div class="footer-left">
            <p>Chat de Hamza - Systme de gestion vtrinaire</p>
          </div>

        <div class="footer-center">
            <p>Par <img  width="80px" height="80px"  src="${imageGitHub}" alt="MilouMed" /></p>
          </div>

          
          <div class="footer-right">
            <p>Document gnr le ${dateStr}  ${timeStr}</p>
          </div>
        </div>
      </div>

      <div class="no-print" style="text-align: center; margin-top: 20px; padding: 15px;">
        <button onclick="window.print()" style="padding: 10px 20px; background: #4CAF50; color: white; border: none; border-radius: 5px; cursor: pointer; font-size: 14px; font-weight: 600;">
           Imprimer le PDF
        </button>
        <button onclick="window.close()" style="padding: 10px 20px; background: #f44336; color: white; border: none; border-radius: 5px; cursor: pointer; font-size: 14px; font-weight: 600; margin-left: 10px;">
           Fermer
        </button>
      </div>
    </body>
    </html>
  `;

    // crire le contenu dans la nouvelle fentre
    printWindow.document.write(content);
    printWindow.document.close();

    // Dclencher l'impression automatiquement aprs un court dlai
    setTimeout(() => {
      printWindow.print();
    }, 500);
  };
  // When user clicks trash icon
  const handleDelete = (index) => {
    setDeleteIndex(index);
    setShowDeleteConfirm(true);
  };

  // User confirmed deletion -> call parent with the chat id (preferred) or index
  const confirmDelete = () => {
    if (deleteIndex === null) {
      setShowDeleteConfirm(false);
      return;
    }

    const patient = patients[deleteIndex];
    const id = patient && patient.id ? patient.id : deleteIndex; // fallback to index
    // call parent callback with id (or index if id missing)
    onDelete(id, deleteIndex); // pass both id and index so parent can decide
    setShowDeleteConfirm(false);
    setDeleteIndex(null);
  };
  return (
    <div className="list-page">
      <h1 className="page-title text-center mb-3">
        <b>
          <i className="fas fa-cat me-2"></i>
          Liste des Chats ({patients.length})
          <i className="fas fa-cat ms-2" style={{ transform: 'rotateY(180deg)' }}  ></i>
        </b>
      </h1>



      <div className="div-btn-colone">
        <Button onClick={() => setShowColumns(!showColumns)}>
          <i className="fa-solid fa-list-check"></i> Les Colonnes
        </Button>

        {showColumns && (
          <ul className='lits-colones'>

            <li>
              <input
                type="checkbox"
                id="Rotation"
                className="me-2"
                checked={visibleColumns.ratation}
                onChange={() => setVisibleColumns({ ...visibleColumns, ratation: !visibleColumns.ratation })}
              />
              <label htmlFor="Rotation" className="d-flex align-items-center">
                <i className="fa-solid fa-arrows-rotate"></i>Rotation
              </label>
            </li>
            <li>
              <input
                type="checkbox"
                id="dateEntree"
                className="me-2"
                checked={visibleColumns.dateEntree}
                onChange={() => setVisibleColumns({ ...visibleColumns, dateEntree: !visibleColumns.dateEntree })}
              />
              <label htmlFor="dateEntree" className="d-flex align-items-center">
                <i className="fas fa-paw me-1"></i>Date entre
              </label>
            </li>


            <li>
              <input type="checkbox" id="dateSortie" className="me-2"
                checked={visibleColumns.dateSortie}
                onChange={() => setVisibleColumns({ ...visibleColumns, dateSortie: !visibleColumns.dateSortie })}
              />
              <label htmlFor="dateSortie" className="d-flex align-items-center">
                <i className="fas fa-calendar-check me-1"></i>Date sortie
              </label>
            </li>

            <li>
              <input type="checkbox" id="vacc1" className="me-2"
                checked={visibleColumns.vacc1}
                onChange={() => setVisibleColumns({ ...visibleColumns, vacc1: !visibleColumns.vacc1 })}
              />
              <label htmlFor="vacc1" className="d-flex align-items-center">
                <i className="fas fa-syringe me-1"></i>1re vaccination
              </label>
            </li>

            <li>
              <input type="checkbox" id="vacc2" className="me-2"
                checked={visibleColumns.vacc2}
                onChange={() => setVisibleColumns({ ...visibleColumns, vacc2: !visibleColumns.vacc2 })}
              />
              <label htmlFor="vacc2" className="d-flex align-items-center">
                <i className="fas fa-syringe me-1"></i>2me vaccination
              </label>
            </li>

            <li>
              <input type="checkbox" id="sterilisation" className="me-2"
                checked={visibleColumns.sterilisation}
                onChange={() => setVisibleColumns({ ...visibleColumns, sterilisation: !visibleColumns.sterilisation })}
              />
              <label htmlFor="sterilisation" className="d-flex align-items-center">
                <i className="fas fa-hospital me-1"></i>Strilisation
              </label>
            </li>

            <li>
              <input type="checkbox" id="vermifuges" className="me-2"
                checked={visibleColumns.vermifuges}
                onChange={() => setVisibleColumns({ ...visibleColumns, vermifuges: !visibleColumns.vermifuges })}
              />
              <label htmlFor="vermifuges" className="d-flex align-items-center">
                <i className="fas fa-pills me-1"></i>Vermifuges
              </label>
            </li>

            <li>
              <input type="checkbox" id="poids" className="me-2"
                checked={visibleColumns.poids}
                onChange={() => setVisibleColumns({ ...visibleColumns, poids: !visibleColumns.poids })}
              />
              <label htmlFor="poids" className="d-flex align-items-center">
                <i className="fas fa-weight me-1"></i>Poids (kg)
              </label>
            </li>

            <li>
              <input type="checkbox" id="visiteVet" className="me-2"
                checked={visibleColumns.visiteVet}
                onChange={() => setVisibleColumns({ ...visibleColumns, visiteVet: !visibleColumns.visiteVet })}
              />
              <label htmlFor="visiteVet" className="d-flex align-items-center">
                <i className="fas fa-stethoscope me-1"></i>Visite vtrinaire
              </label>
            </li>

            <li>
              <input type="checkbox" id="paiement" className="me-2"

                checked={visibleColumns.paiement}
                onChange={() => setVisibleColumns({ ...visibleColumns, paiement: !visibleColumns.paiement })}
              />
              <label htmlFor="paiement" className="d-flex align-items-center">
                <i className="fas fa-money-bill me-1"></i>Paiement
              </label>
            </li>


          </ul>
        )}
      </div>






      <div className="table-wrapper">
        <Table striped bordered hover
          className={`main-table ${visibleColumns.ratation ? ' tabl-hamza ' : ''}`}
        >
          <thead className="th-hamza">
            <tr className="bg-dark text-white">


              <th className="align-middle text-center">
                <i className="fas fa-cog"></i>
              </th>

              <th className="align-middle text-center">
                <i className="fas fa-paw me-1"></i><span>Nom</span>
              </th>

              {visibleColumns.dateEntree && (
                <th className="align-middle text-center">
                  <i className="fas fa-calendar me-1"></i><span>Date entre</span>
                </th>
              )}

              {visibleColumns.dateSortie && (
                <th className="align-middle text-center">
                  <i className="fas fa-calendar-check me-1"></i><span>Date sortie</span>
                </th>
              )}

              {visibleColumns.vacc1 && (
                <th className="align-middle text-center">
                  <i className="fas fa-syringe me-1"></i><span>1re vaccination</span>
                </th>
              )}

              {visibleColumns.vacc2 && (
                <th className="align-middle text-center">
                  <i className="fas fa-syringe me-1"></i><span>2me vaccination</span>
                </th>
              )}

              {visibleColumns.sterilisation && (
                <th className="align-middle text-center">
                  <i className="fas fa-hospital me-1"></i><span>Strilisation</span>
                </th>
              )}

              {visibleColumns.vermifuges && (
                <th className="align-middle text-center">
                  <i className="fas fa-pills me-1"></i><span>Vermifuges</span>
                </th>
              )}

              {visibleColumns.poids && (
                <th className="align-middle text-center">
                  <i className="fas fa-weight me-1"></i><span>Poids (kg)</span>
                </th>
              )}

              {visibleColumns.visiteVet && (
                <th className="align-middle text-center">
                  <i className="fas fa-stethoscope me-1"></i><span>Visite vtrinaire</span>
                </th>
              )}

              {visibleColumns.paiement && (
                <th className="align-middle text-center">
                  <i className="fas fa-money-bill me-1"></i><span>Paiement</span>
                </th>
              )}



            </tr>
          </thead>
          <tbody>
            {patients.map((patient, index) => (
              <tr key={patient.id ?? index}>
                <td className="align-middle text-center">
                  <div className="btn-group">
                    <Button variant="outline-primary" size="sm" onClick={() => onEdit(index)} title="Modifier">
                      <i className="fas fa-edit"></i>
                    </Button>
                    <Button variant="outline-success" size="sm" onClick={() => generatePDF(patient)} title="Imprimer PDF">
                      <i className="fas fa-file-pdf"></i>
                    </Button>
                    <Button variant="outline-danger" size="sm" onClick={() => handleDelete(index)} title="Supprimer">
                      <i className="fas fa-trash-alt"></i>
                    </Button>
                  </div>
                </td>

                <td className="align-middle text-center"><span className="fw-bold">{patient.nom || '-'}</span></td>

                {visibleColumns.dateEntree && (
                  <td className="align-middle text-center">{patient.dateEntree || '-'}</td>
                )}
                {visibleColumns.dateSortie && (
                  <td className="align-middle text-center">{patient.dateSortie || '-'}</td>
                )}
                {visibleColumns.vacc1 && (
                  <td className="align-middle text-center">{patient.vaccination1 || '-'}</td>
                )}
                {visibleColumns.vacc2 && (
                  <td className="align-middle text-center">{patient.vaccination2 || '-'}</td>
                )}
                {visibleColumns.sterilisation && (
                  <td className="align-middle text-center">{patient.sterilisation || '-'}</td>
                )}
                {visibleColumns.vermifuges && (

                  <td className="align-middle text-center">
                    {Array.isArray(patient.vermifuges) && patient.vermifuges.length > 0 ? (
                      patient.vermifuges.map((v, idx) => (
                        <span key={idx} className="badge bg-warning text-dark me-1 mb-1 d-inline-block">
                          {v.date}: {v.remarque}
                        </span>
                      ))
                    ) : <span className="text-muted">-</span>}
                  </td>
                )}
                {visibleColumns.poids && (

                  <td className="align-middle text-center">
                    {Array.isArray(patient.poids) && patient.poids.length > 0 ? (
                      patient.poids.map((p, idx) => (
                        <span key={idx} className="badge bg-danger me-1 mb-1 d-inline-block">
                          {p.date}: {p.valeur} kg
                        </span>
                      ))
                    ) : <span className="text-muted">-</span>}
                  </td>
                )}
                {visibleColumns.visiteVet && (

                  <td className="align-middle text-center">
                    {Array.isArray(patient.visitesVeterinaires) && patient.visitesVeterinaires.length > 0 ? (
                      patient.visitesVeterinaires.map((v, idx) => (
                        <span key={idx} className="badge bg-primary me-1 mb-1 d-inline-block">
                          {v.date}: {v.remarque}
                        </span>
                      ))
                    ) : <span className="text-muted">-</span>}
                  </td>
                )}
                {visibleColumns.paiement && (

                  <td className="align-middle text-center">
                    {Array.isArray(patient.paiements) && patient.paiements.length > 0 ? (
                      patient.paiements.map((p, idx) => (
                        <span key={idx} className="badge bg-success me-1 mb-1 d-inline-block">
                          {p.date}: {p.details}
                        </span>
                      ))
                    ) : <span className="text-muted">-</span>}
                  </td>
                )}

              </tr>
            ))
            }
          </tbody >
        </Table >
      </div >

      {/* Modal confirmation suppression */}
      <Modal show={showDeleteConfirm} onHide={() => setShowDeleteConfirm(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Confirmation de suppression</Modal.Title>
        </Modal.Header>
        <Modal.Body>Voulez-vous vraiment supprimer cette fiche de chat ?</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={() => setShowDeleteConfirm(false)}>Annuler</Button>
          <Button variant="danger" onClick={confirmDelete}>Supprimer</Button>
        </Modal.Footer>
      </Modal>
    </div >
  );
}

export default ListPage;
