import { useState } from "react";
import { Modal, Button, Form, Row, Col } from "react-bootstrap";

function AddPage({ onAdd, onClose, show }) {
  const [formData, setFormData] = useState({
    nom: "",
    dateEntree: new Date().toISOString().split("T")[0],
    dateSortie: "",
    vaccination1: "",
    vaccination2: "",
    sterilisation: "",
    poids: [],
    paiements: [],
    vermifuges: [],
    visitesVeterinaires: [],
    // Champs temporaires pour ajout
    poidsDate: new Date().toISOString().split("T")[0],
    poidsValeur: "",
    paiementDate: new Date().toISOString().split("T")[0],
    paiementDetails: "",
    vermifugeDate: new Date().toISOString().split("T")[0],
    vermifugeRemarque: "",
    visiteDate: new Date().toISOString().split("T")[0],
    visiteRemarque: ""
  });

  const [deleteIdx, setDeleteIdx] = useState(null);
  const [deleteKey, setDeleteKey] = useState(null);
  const [showConfirm, setShowConfirm] = useState(false);

  // Gestion modale suppression
  const handleDelete = (key, idx) => {
    setDeleteIdx(idx);
    setDeleteKey(key);
    setShowConfirm(true);
  };

  const confirmDelete = () => {
   
    const copy = [...formData[deleteKey]];
    copy.splice(deleteIdx, 1);
    setFormData({ ...formData, [deleteKey]: copy });
    setShowConfirm(false);
    setDeleteIdx(null);
    setDeleteKey(null);
  };

  // Gestion ajout dynamique
  const handleAddItem = (key, dateField, valueField, valueName) => {
    if (formData[dateField] && formData[valueField]) {
      const newItem = {
        date: formData[dateField],
        [valueName]: formData[valueField]
      };
      setFormData({
        ...formData,
        [key]: [...formData[key], newItem],
        [dateField]: new Date().toISOString().split("T")[0],
        [valueField]: ""
      });
    }
  };

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    const dataToSend = {
      nom: formData.nom,
      dateEntree: formData.dateEntree,
      dateSortie: formData.dateSortie,
      vaccination1: formData.vaccination1,
      vaccination2: formData.vaccination2,
      sterilisation: formData.sterilisation,
      poids: formData.poids,
      paiements: formData.paiements,
      vermifuges: formData.vermifuges,
      visitesVeterinaires: formData.visitesVeterinaires
    };

    onAdd(dataToSend);

    // Rinitialiser le formulaire
    setFormData({
      nom: "",
      dateEntree: new Date().toISOString().split("T")[0],
      dateSortie: "",
      vaccination1: "",
      vaccination2: "",
      sterilisation: "",
      poids: [],
      paiements: [],
      vermifuges: [],
      visitesVeterinaires: [],
      poidsDate: new Date().toISOString().split("T")[0],
      poidsValeur: "",
      paiementDate: new Date().toISOString().split("T")[0],
      paiementDetails: "",
      vermifugeDate: new Date().toISOString().split("T")[0],
      vermifugeRemarque: "",
      visiteDate: new Date().toISOString().split("T")[0],
      visiteRemarque: ""
    });
  };

  return (
    <Modal show={show} onHide={onClose} centered dialogClassName="modal-xl">
      <Modal.Header closeButton>
        <Modal.Title>Ajouter un Nouveau Chat De Hamza</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form onSubmit={handleSubmit}>
          {/* =================== Informations de base =================== */}
          <Row>
            <Col xs={12} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>Nom</Form.Label>
                <Form.Control
                  name="nom"
                  value={formData.nom}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col xs={6} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>Date d'entre</Form.Label>
                <Form.Control
                  name="dateEntree"
                  type="date"
                  value={formData.dateEntree}
                  onChange={handleChange}
                  required
                />
              </Form.Group>
            </Col>
            <Col xs={6} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>Date sortie</Form.Label>
                <Form.Control
                  name="dateSortie"
                  type="date"
                  value={formData.dateSortie}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs={12} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>1re vaccination</Form.Label>
                <Form.Control
                  name="vaccination1"
                  type="date"
                  value={formData.vaccination1}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>2me vaccination</Form.Label>
                <Form.Control
                  name="vaccination2"
                  type="date"
                  value={formData.vaccination2}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
            <Col xs={12} md={4}>
              <Form.Group className="mb-2">
                <Form.Label>Strilisation</Form.Label>
                <Form.Control
                  name="sterilisation"
                  type="date"
                  value={formData.sterilisation}
                  onChange={handleChange}
                />
              </Form.Group>
            </Col>
          </Row>

          {/* =================== Poids et Paiements =================== */}
          <Row>
            <Col xs={12} md={6}>
              <Form.Group className="mb-2">
                <Form.Label>Poids (kg)</Form.Label>
                <div className="d-flex mb-2 flex-column flex-md-row">
                  <Form.Control
                    name="poidsDate"
                    type="date"
                    value={formData.poidsDate}
                    onChange={handleChange}
                    className="me-md-2 mb-2 mb-md-0"
                  />
                  <Form.Control
                    name="poidsValeur"
                    type="number"
                    value={formData.poidsValeur}
                    onChange={handleChange}
                    placeholder="Poids"
                    step="0.1"
                    className="me-md-2"
                  />
                  <Button
                    variant="info"
                    type="button"
                    onClick={() => handleAddItem("poids", "poidsDate", "poidsValeur", "valeur")}
                  >
                    +
                  </Button>
                </div>
                <ul className="list-poids bg-danger p-2 text-white rounded">
                  {formData.poids.map((p, idx) => (
                    <li key={idx} className="d-flex align-items-center justify-content-between">
                      <div>{p.date} : <span className="badge bg-light text-dark">{p.valeur} kg</span></div>
                      <Button variant="outline-light" size="sm" onClick={() => handleDelete("poids", idx)}>Suppr</Button>
                    </li>
                  ))}
                </ul>
              </Form.Group>
            </Col>

            <Col xs={12} md={6}>
              <Form.Group className="mb-2">
                <Form.Label>Paiements</Form.Label>
                <div className="d-flex mb-2 flex-column flex-md-row">
                  <Form.Control
                    name="paiementDate"
                    type="date"
                    value={formData.paiementDate}
                    onChange={handleChange}
                    className="me-md-2 mb-2 mb-md-0"
                  />
                  <Form.Control
                    name="paiementDetails"
                    type="text"
                    value={formData.paiementDetails}
                    onChange={handleChange}
                    placeholder="Dtails"
                    className="me-md-2"
                  />
                  <Button
                    variant="info"
                    type="button"
                    onClick={() => handleAddItem("paiements", "paiementDate", "paiementDetails", "details")}
                  >
                    +
                  </Button>
                </div>
                <ul className="list-paiements bg-success p-2 text-white rounded">
                  {formData.paiements.map((p, idx) => (
                    <li key={idx} className="d-flex align-items-center justify-content-between">
                      <div>{p.date} : <span className="badge bg-light text-dark">{p.details}</span></div>
                      <Button variant="outline-light" size="sm" onClick={() => handleDelete("paiements", idx)}>Suppr</Button>
                    </li>
                  ))}
                </ul>
              </Form.Group>
            </Col>
          </Row>

          {/* =================== Vermifuges et Visites =================== */}
          <Row>
            <Col xs={12} md={6}>
              <Form.Group className="mb-2">
                <Form.Label>Vermifuges</Form.Label>
                <div className="d-flex mb-2 flex-column flex-md-row">
                  <Form.Control
                    name="vermifugeDate"
                    type="date"
                    value={formData.vermifugeDate}
                    onChange={handleChange}
                    className="me-md-2 mb-2 mb-md-0"
                  />
                  <Form.Control
                    name="vermifugeRemarque"
                    type="text"
                    value={formData.vermifugeRemarque}
                    onChange={handleChange}
                    placeholder="Remarque"
                    className="me-md-2"
                  />
                  <Button
                    variant="info"
                    type="button"
                    onClick={() => handleAddItem("vermifuges", "vermifugeDate", "vermifugeRemarque", "remarque")}
                  >
                    +
                  </Button>
                </div>
                <ul className="list-vermifuges bg-warning p-2 text-dark rounded">
                  {formData.vermifuges.map((v, idx) => (
                    <li key={idx} className="d-flex align-items-center justify-content-between">
                      <div>{v.date} : <span className="badge bg-light text-dark">{v.remarque}</span></div>
                      <Button variant="outline-dark" size="sm" onClick={() => handleDelete("vermifuges", idx)}>Suppr</Button>
                    </li>
                  ))}
                </ul>
              </Form.Group>
            </Col>

            <Col xs={12} md={6}>
              <Form.Group className="mb-2">
                <Form.Label>Visites vtrinaires</Form.Label>
                <div className="d-flex mb-2 flex-column flex-md-row">
                  <Form.Control
                    name="visiteDate"
                    type="date"
                    value={formData.visiteDate}
                    onChange={handleChange}
                    className="me-md-2 mb-2 mb-md-0"
                  />
                  <Form.Control
                    name="visiteRemarque"
                    type="text"
                    value={formData.visiteRemarque}
                    onChange={handleChange}
                    placeholder="Remarque"
                    className="me-md-2"
                  />
                  <Button
                    variant="info"
                    type="button"
                    onClick={() => handleAddItem("visitesVeterinaires", "visiteDate", "visiteRemarque", "remarque")}
                  >
                    +
                  </Button>
                </div>
                <ul className="list-visites bg-primary p-2 text-white rounded">
                  {formData.visitesVeterinaires.map((v, idx) => (
                    <li key={idx} className="d-flex align-items-center justify-content-between">
                      <div>{v.date} : <span className="badge bg-light text-dark">{v.remarque}</span></div>
                      <Button variant="outline-light" size="sm" onClick={() => handleDelete("visitesVeterinaires", idx)}>Suppr</Button>
                    </li>
                  ))}
                </ul>
              </Form.Group>
            </Col>
          </Row>

          {/* =================== Boutons =================== */}
          <Row>
            <div className="d-flex justify-content-end mt-3">
              <Button variant="secondary" onClick={onClose} className="me-2">Annuler</Button>
              <Button type="submit" variant="success">Ajouter</Button>
            </div>
          </Row>
        </Form>

        {/* =================== MODALE CONFIRMATION =================== */}
        <Modal show={showConfirm} onHide={() => setShowConfirm(false)} centered>
          <Modal.Header closeButton>
            <Modal.Title>Confirmation</Modal.Title>
          </Modal.Header>
          <Modal.Body>Voulez-vous vraiment supprimer cet lment ?</Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={() => setShowConfirm(false)}>Annuler</Button>
            <Button variant="danger" onClick={confirmDelete}>Supprimer</Button>
          </Modal.Footer>
        </Modal>

      </Modal.Body>
    </Modal>
  );
}

export default AddPage;
