import { useState, useEffect } from "react";
import { Button } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import * as XLSX from "xlsx";
import AddPage from './pages/AddPage';
import EditPage from './pages/EditPage';
import ListPage from './pages/ListPage';

function App() {
  const [Chats, setChats] = useState([]);
  const [editingIndex, setEditingIndex] = useState(null);
  const [showAddModal, setShowAddModal] = useState(false);
  const [showEditModal, setShowEditModal] = useState(false);

  useEffect(() => {
    fetch("http://localhost/hamza-chats/api/data-api.php")
      .then(res => res.json())
      .then(data => setChats(Array.isArray(data) ? data : []));
  }, []);
 
 

  // App.js (extrait) ----------------------------------------- 
 const handleDeletePatient = async (idOrIndex, maybeIndex) => {
  let id = idOrIndex;
  if ((typeof idOrIndex === 'number') && Chats[idOrIndex] && Chats[idOrIndex].id) {
    id = Chats[idOrIndex].id;
  }

  if (!id) return alert("Impossible de supprimer : ID manquant");

  // Supprimer localement
  setChats(prev => prev.filter(c => c.id !== id));

  // Supprimer ct serveur
  try {
    const res = await fetch("http://localhost/hamza-chats/api/data-api.php", {
      method: "DELETE",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ id })
    });
    const json = await res.json();
    if (json.status === "ok") {
      alert("Chat supprim et serveur mis  jour !");
    } else {
      alert("Erreur serveur : " + JSON.stringify(json));
    }
  } catch (err) {
    alert("Erreur rseau lors de la suppression : " + err);
  }
};



// -----------------------------------------------------------



  const editPatient = (index) => {
    setEditingIndex(index);
    setShowEditModal(true);
  };
  const updatePatient = async (index, updatedData) => {
    if (index === null || Chats[index] === undefined) {
      console.error("Index invalide :", index, Chats);
      return;
    }

    // Ajouter l'id si absent
    if (!updatedData.id) {
      updatedData.id = Chats[index].id;
    }

    const updated = [...Chats];
    updated[index] = updatedData;
    setChats(updated);
    setShowEditModal(false);

    try {
      const res = await fetch("http://localhost/hamza-chats/api/data-api.php", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify([updatedData])
      });
      const json = await res.json();
      if (json.status === "ok") {
        alert("Chat modifi et enregistr sur le serveur !");
      } else {
        alert("Erreur serveur : " + JSON.stringify(json));
      }
    } catch (err) {
      alert("Erreur rseau : " + err);
    }
  };


  const exportExcel = () => {
    const ChatsFormatted = Chats.map(chat => ({
      Nom: chat.nom || '',
      "Date entre": chat.dateEntree || '',
      "Date sortie": chat.dateSortie || '',
      "1re vaccination": chat.vaccination1 || '',
      "2me vaccination": chat.vaccination2 || '',
      Strilisation: chat.sterilisation || '',
      Vermifuges: chat.vermifuges?.map(v => `${v.date}: ${v.remarque}`).join(" | ") || '',
      Poids: chat.poids?.map(p => `${p.date}: ${p.valeur} kg`).join(" | ") || '',
      "Visites vtrinaires": chat.visitesVeterinaires?.map(v => `${v.date}: ${v.remarque}`).join(" | ") || '',
      Paiements: chat.paiements?.map(p => `${p.date}: ${p.details}`).join(" | ") || ''
    }));

    const worksheet = XLSX.utils.json_to_sheet(ChatsFormatted);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Chats");
    XLSX.writeFile(workbook, "Chats_Export.xlsx");
  };


  const saveToServer = async () => {
    const filteredChats = Chats.filter(chat => chat.nom && chat.nom.trim() !== '');
    if (filteredChats.length === 0) return alert("Aucun chat valide  enregistrer.");

    const cleanedData = filteredChats.map(chat => ({
      id: chat.id,
      nom: chat.nom,
      dateEntree: chat.dateEntree || '',
      dateSortie: chat.dateSortie || '',
      vaccination1: chat.vaccination1 || '',
      vaccination2: chat.vaccination2 || '',
      sterilisation: chat.sterilisation || '',
      poids: chat.poids?.map(p => ({ date: p.date, valeur: p.valeur })) || [],
      paiements: chat.paiements?.map(p => ({ date: p.date, details: p.details })) || [],
      vermifuges: chat.vermifuges?.map(v => ({ date: v.date, remarque: v.remarque })) || [],
      visitesVeterinaires: chat.visitesVeterinaires?.map(v => ({ date: v.date, remarque: v.remarque })) || [],
    }));

    console.log("Envoi au serveur :", cleanedData);

    try {
      const res = await fetch("http://localhost/hamza-chats/api/data-api.php", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(cleanedData)
      });
      const json = await res.json();
      console.log("Rponse serveur :", json);

      if (json.status === "ok") {
        alert("Donnes enregistres sur le serveur.");
      } else {
        alert("Erreur serveur : " + JSON.stringify(json));
      }
    } catch (err) {
      alert("Erreur rseau : " + err);
    }
  };




  return (
    <div className="container-1">
      {/* Barre de boutons */}
      <div className="d-flex flex-wrap justify-content-start align-items-center m-4 gap-2">
        <Button variant="primary" size="sm" onClick={() => setShowAddModal(true)}>
          Ajouter un Chat
        </Button>

        {<Button variant="success" size="sm" onClick={exportExcel}>
          Exporter en Excel
        </Button>/*
 Liste des chats */}
        <Button
          variant="success"
          size="sm"
          onClick={saveToServer}
        >
          Enregistrer
        </Button>


      </div>

      {/* Liste des chats */}
      <div className="card p-3 shadow mt-2" style={{ overflowX: "auto" }}>
        <ListPage
          patients={Chats}
          onEdit={editPatient}
          onDelete={handleDeletePatient}
        />
      </div>

      {/* Modal Ajouter */}
      <AddPage
        show={showAddModal}
        onClose={() => setShowAddModal(false)}
        onAdd={async (newChat) => {
          if (!newChat.nom) return alert("Le nom est obligatoire !");
          if (!newChat.dateEntree) return alert("La date d'entre est obligatoire !");

          try {
            const res = await fetch("http://localhost/hamza-chats/api/data-api.php", {
              method: "POST",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify([newChat])
            });
            const json = await res.json();

            if (json.status === "ok") {
              setChats(prev => [...prev, newChat]);
              setShowAddModal(false);
              
            } else {
              alert("Erreur serveur : " + JSON.stringify(json));
            }
          } catch (err) {
            alert("Erreur rseau : " + err);
          }
        }}


      />

      {/* Modal diter */}
      {editingIndex !== null && (
        <EditPage
          show={showEditModal}
          onClose={() => setShowEditModal(false)}
          chatData={{ ...Chats[editingIndex], index: editingIndex }} // <-- ajouter index
          onUpdate={updatePatient}
        />
      )}

    </div>
  );
}

export default App;
